import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomePage } from './home.page';

const routes: Routes = [
  {
    path: 'home',
    component: HomePage,
    children:[
      {
          path:"steuerung",
          loadChildren:() => import('../pages/steuerung/steuerung.module').then(m => m.SteuerungPageModule)
      },
      {
          path:"onsite",
            loadChildren:() => import('../pages/onsite/onsite.module').then(m => m.OnsitePageModule)
      }
  ]

}];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HomeRouter {}